<template>
  <div class="app-wrapper" :class="{ hideSidebar: !sidebarOpen }">
    <Sidebar :open="sidebarOpen" @toggle-sidebar="toggleSidebar" />
    <div class="main-container">
      <Navbar :sidebar-open="sidebarOpen" @toggle-sidebar="toggleSidebar" />
      <div class="app-main">
        <div class="details">
          <template v-if="job">
            <h1>รายละเอียดงาน: {{ job.route }}</h1>
            <p>ภูมิภาค: {{ job.region }}</p>
            <p>ชนิดของรถ: {{ job.vehicleType }}</p>
            <p>ระยะทาง: {{ job.distance }} KM</p>
            <p>ค่าเที่ยว: {{ job.fare }} บาท</p>
          </template>
          <template v-else>
            <h1>ไม่พบข้อมูลงาน</h1>
          </template>
          
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Sidebar from "@/components/Sidebar.vue";
import Navbar from "@/components/Navbar.vue";

export default {
  name: "Details",
  props: {
    id: {
      type: Number,
      required: true,
    },
    jobs: {
      type: Array,
      required: true,
    }
  },
  components: { Sidebar, Navbar },
  data() {
    return {
      sidebarOpen: true,
      job: null,
    };
  },
  created() {
    this.findJob();
  },
  methods: {
    toggleSidebar() {
      this.sidebarOpen = !this.sidebarOpen;
    },
    findJob() {
      this.job = this.jobs.find(job => job.id === this.id);
      if (!this.job) {
        console.error("Job not found with ID:", this.id);
      }
    },
  },
};
</script>

<style scoped>
.app-wrapper {
  display: flex;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  overflow: hidden;
}

.main-container {
  flex: 1;
  margin-left: 220px;
  transition: margin-left 0.3s;
  padding: 8px;
  display: flex;
  flex-direction: column;
  height: calc(100vh - 16px);
  overflow: hidden;
}

.app-main {
  flex: 1;
  display: flex;
  flex-direction: column;
  height: calc(100% - 60px);
  overflow-y: auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.details {
  padding: 20px;
  flex: 1;
  overflow: auto;
}

.hideSidebar .main-container {
  margin-left: 54px;
}
</style>
